@import url('https://fonts.googleapis.com/css2?family=Courgette&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Baloo+Bhaina+2&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Josefin+Sans&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Kanit&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Dancing+Script&display=swap');

.logo-jasurlive {
    background-color: #f0f8ffb9;
    color: #333;
    border-radius: 12px;
    border: 1px solid #08001933;
    padding: 0.1rem 0;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    width: max-content;
    padding: 0.1rem 1rem;
    margin: 5px auto 0;
    margin-bottom: 5px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.logo-text {
    font-family: 'Courgette', cursive, sans-serif;
    font-size: 1.4em;
    font-weight: bold;
    background: linear-gradient(45deg, #ffd700, #ff9900, #ff69b4, #ff34b3, #ff008e, #c300ff, #8e00ff, #4a00ff, #0091ff, #009fff);
    -webkit-background-clip: text;
    background-clip: text;
    -webkit-text-fill-color: transparent;
    animation: gradient 6s ease infinite;
}

.logo-text:hover {
    animation: hover-gradient 1.5s ease infinite;
}

@keyframes gradient {
    0% {
        background-position: 0% 50%;
    }

    100% {
        background-position: 100% 50%;
    }
}

@keyframes hover-gradient {
    0% {
        background-position: 0% 50%;
        text-shadow: 0 0 3px rgba(255, 255, 255, 0.5);
        transform: scale(1);
    }

    50% {
        background-position: 100% 50%;
        transform: scale(1.1);
    }

    100% {
        background-position: 0% 50%;
        text-shadow: 0 0 3px rgba(255, 255, 255, 0.5);
        transform: scale(1);
    }
}

.deadpool {
    display: inline-flex;
    width: 1.2em;
    height: 1.2em;
    vertical-align: middle;
}